.popup__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}